<script>
import { active } from '../../../src'
export let links = []
</script>

<div class='nav'>
  {#each links as e}
    {#if e}
      <a
        class:active={e === $active} 
        href={e.$$href}
        > {e.$$pathname}
      </a>
    {:else}
      <div class='divider'></div>
    {/if}
  {/each}
</div>

<style>
.nav {
  float: left;
  width: 100px;
  height: 100%;
  padding-top: 10px;
  background: dimgray;
}

a {
  display: block;
  padding: 3px;
  padding-left: 10px;
  color: white;
  text-decoration: none;
}

a:hover {
  background: gray;
}

a.active {
  color: darkslategray;
  background: lightgray;
}

.divider {
  height: 1px;
  background: lightgray;
}
</style>