<script>
import { matches, active } from '../../../src'
export let route = {}
export let deep = true
export let pad = 0
$: style = `padding-left:${++pad * 10}px`
</script>

{#each Object.values(route) as r}
  <a
    class:active={r === $active} 
    href={r.$$href}
    {style}
    > {r.$$pathname}
  </a>
  {#if deep && $matches.includes(r)}
    <svelte:self route={r} {deep} {pad}/>
    <div class='divider'></div>
  {/if}
{/each}
{#if !deep}
  <div class='divider'></div>
{/if}

<style>
a {
  display: block;
  padding: 3px 10px;
  color: white;
  text-decoration: none;
}

a:hover {
  background: gray;
}

a.active {
  color: darkslategray;
  background: lightgray;
}

.divider {
  height: 1px;
  background: lightgray;
}
</style>